<template>
    <el-main>
        <el-row>
            <el-button @click="buttonClick">
                Login
            </el-button>
            <el-button @click="loginGG">
                Google
            </el-button>
        </el-row>
        <!-- <el-row class="row-abc" style="margin-top: 10px">
            <el-time-select
                v-model="value"
                :picker-options="{
                    start: '08:30',
                    step: '00:30',
                    end: '18:30'
                }"
                @change="changeDate"
                placeholder="Select time">
            </el-time-select>
        </el-row> -->
    </el-main>
</template>

<script>
// import { mapState } from 'vuex'
import { mapActions } from 'vuex'
import firebase from 'firebase/app'
import 'firebase/auth'

export default {
  data () {
    return {
      value: '',
      GGProvider: {}
    }
  },
  computed: {
    // ...mapState('welcome', [])
  },
  mounted () {
    this.initPage()
  },
  methods: {
    ...mapActions('welcome', ['login']),
    // Init Page
    initPage () {
      const firebaseConfig = {
        apiKey: 'AIzaSyC__udfBeXgjaCmtTFQ0P-tCzdM1zhvb6g',
        authDomain: 'authengg-a25b9.firebaseapp.com',
        databaseURL: 'https://authengg-a25b9.firebaseio.com',
        projectId: 'authengg-a25b9',
        storageBucket: 'authengg-a25b9.appspot.com',
        messagingSenderId: '217900106617',
        appId: '1:217900106617:web:3ca4208e39f0a78a50e5a8',
        measurementId: 'G-W8YTJC4GW4'
      }

      if (!firebase.apps.length) firebase.initializeApp(firebaseConfig)
      // firebase.analytics()

      this.GGProvider = new firebase.auth.GoogleAuthProvider()
    },
    async loginGG () {
      try {
        const result = await firebase.auth().signInWithPopup(this.GGProvider)

        console.log(result)

        const idToken = await firebase.auth().currentUser.getIdToken(true)
        console.log(idToken)

        // this.$router.push({ name: 'Test1', params: {id: 123} })
      } catch (e) {
        console.error('Error', e)
      }
    },
    // changeDate () {
    // //   this.$message(this.value)
    //   this.$alert(this.value, 'Date', {
    //     confirmButtonText: 'OK'
    //   })
    // },
    buttonClick () {
      this.login({username: 'leminhtuan', password: '1'}).then(res => {
        sessionStorage.setItem('ACCESS_TOKEN', 'Bearer ' + res.data.access_token)
        sessionStorage.setItem('USERNAME', res.data.user.username)
        sessionStorage.setItem('FULLNAME', res.data.user.fullname)
        sessionStorage.setItem('EMAIL', res.data.user.email)
        this.$router.push({ name: 'Test1', params: {id: 123} })
      }).catch(e => console.error(e))
    }
  }
}
</script>

<style>
@import '../assets/css/abc.css';
</style>



